import axios from 'axios'
export default {
  name: 'runMission',
  data () {
    return {
      mission: Array
    }
  },
  methods: {
    getMissionList () {
      axios.get(this.url + '/mission/module/' + sessionStorage.getItem('moduleId'))
        .then(function (res) {
          this.mission = res.data
          console.log(this.mission)
        })
    }
  },
  mounted () {
    this.getMissionList()
  }
}



